<template>
  <header class="top">
    <div class="container-fluid">
      <div class="header-top-wrapper">
        <div class="header-top-bar-wrapper" v-if="isMobile">
          <div class="bar-controller">
            <a @click.prevent="openModal">
              <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36" style="fill: none; stroke: #fff; stroke-linecap: round; stroke-miterlimit: 10; stroke-width: 2px;">
                <line x1="5" y1="9" x2="31" y2="9"/>
                <line x1="5" y1="18.5" x2="31" y2="18.5"/>
                <line x1="5" y1="28" x2="31" y2="28"/>
              </svg>
            </a>
          </div>
        </div>
        <a href="#" v-if="isMobile">
          <img src="../assets/casino-the-world-logo.png" alt="CASINO THE WORLD" class="header-top-logo">
        </a>
        <div class="header-top-auth">
          <a href="#" class="header-top-btn sign-in">Sign in</a>
          <a href="" class="header-top-btn sign-up">Sign up</a>
        </div>
        <div class="header-top-locales" @click="openLangItem">
          <div class="select-lang-icons-btn">
            <div class="icons-btn-inner">
              <a>
                <div class="code-icon">
                  <img src="https://cdn2.softswiss.net/i/flags/rectangular/en.svg" alt="en" class="flag-icon">
                </div>
                <div class="code-code">En</div>
              </a>
            </div>
          </div>
          <div class="select-lang-icons-btn-caret">
            <svg class="arrow-down" ref="arrow" xmlns="http://www.w3.org/2000/svg" width="20" height="12" fill="currentColor" viewBox="0 0 16 16" preserveAspectRatio="xMidYMid slice">
              <path stroke="white" stroke-width="1.5" fill-rule="evenodd" d="M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z"/>
            </svg>
          </div>
          <div class="select-lang-icons-container" ref="langItem">
            <ul class="code-list">
              <li class="code-item">
                <a class="option-link-content">
                  <div class="code-icon">
                    <img src="https://cdn2.softswiss.net/i/flags/rectangular/ja.svg" alt="ja" class="flag-icon">
                  </div>
                  <div class="code-code">Ja</div>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </header>
  <BarModal ref="barModal"></BarModal>
</template>

<script>
import BarModal from './BarModal.vue'
export default {
  data () {
    return {
      isMobile: true
    }
  },
  components: {
    BarModal
  },
  methods: {
    openModal () {
      const barModal = this.$refs.barModal
      barModal.showModal()
    },
    openLangItem () {
      console.log(this.$refs.arrow)
      this.$refs.arrow.classList.toggle('rotate')
      this.$refs.langItem.classList.toggle('itemShow')
    },
    checkWindowWidth () {
      this.isMobile = window.innerWidth <= 1248
    }
  },
  mounted () {
    this.checkWindowWidth()
    window.addEventListener('resize', this.checkWindowWidth)
  },
  beforeUnmount () {
    window.removeEventListener('resize', this.checkWindowWidth)
  }
}
</script>
