import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    children: [
      {
        path: '',
        name: 'main',
        component: () => import('../views/MainSection.vue')
      },
      {
        path: 'termsandconditions',
        component: () => import('../views/TermsAndConditions.vue')
      },
      {
        path: 'responsiblegaming',
        component: () => import('../views/ResponsibleGaming.vue')
      },
      {
        path: 'privacypolicy',
        component: () => import('../views/PrivacyPolicy.vue')
      },
      {
        path: 'KYCPolicy',
        component: () => import('../views/KYCPolicy.vue')
      },
      {
        path: 'underageGaming',
        component: () => import('../views/UnderageGamingPolicy.vue')
      },
      {
        path: 'selfexclusionpolicy',
        component: () => import('../views/SelfExclusionPolicy.vue')
      },
      {
        path: 'support',
        component: () => import('../views/CasinoSupport.vue')
      },
      {
        path: 'games',
        component: () => import('../views/AllGames.vue'),
        children: [
          {
            path: '',
            name: 'All game',
            component: () => import('../components/AllGamesPart.vue')
          }
        ]
      }
    ]
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/:pathMatch(.*)*',
    component: () => import('../views/NotFound.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  linkActiveClass: 'active',
  scrollBehavior () {
    return {
      top: 0
    }
  }
})

export default router
