export default {
  data () {
    return {
      isLoading: true,
      timer: ''
    }
  },
  methods: {
    closeLoading () {
      this.isLoading = false
    }
  },
  mounted () {
    this.timer = setTimeout(this.closeLoading, 2000)
  },
  beforeUnmount () {
    clearTimeout(this.timer)
  }
}
